.single-movie-container {
  height: 100vh;
  width: 100vw;
}

.single-movie {
  display: flex;
  flex-direction: row;
}

.poster {
  border-radius: 10px;
  height: 65vh;
  margin-left: 15%;
  width: 27vw;
}

.movie-info {
  color: white;
  margin-left: 13%;
  width: 43vw;
}

.back-button {
  background-color: #C70039;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 18px;
  font-weight: bold;
  height: 5.5vh;
  margin-bottom: 5%;
  margin-left: 4%;
  margin-top: 5%;
  text-align: center;
  width: 10vw;
}

h3 {
  font-size: 23px;
  color: yellow; 
}

p {
  color: yellow;
  font-size: 18px;
}

.info {
  color: white; 
}

.movie-title {
  font-size: 45px;
  margin-left: 0;
  margin-top: 1%;
}