body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Roboto', sans-serif;
}
.App {
  text-align: center;
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.loading,
.error {
  color: white;
}

.loading {
  font-size: 40px;
  margin-left: 43%;
}

.error, .no-movies {
  font-size: 40px;
  margin-left: 24%;
}

.logo {
  margin-left: 20%;
  margin-top: 2%;
}

.title {
  color: white;
  font-size: 60px;
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 6%;
}

.header-container {
  display: flex;
  height: 25vh;
  justify-content: flex-start;
  width: 100vw;
}
.search-bar {
  height: 29vh;
  margin-left: 6%;
}

.search-field {
  border: white;
  border-radius: 5px;
  font-size: x-large;
  font-weight: bold;
  height: 5.5vh;
  margin-top: 30%;
  width: 13rem;
}

.search-field::-webkit-input-placeholder {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}

.search-field::placeholder {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}

.button {
  background-color: #C70039;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 20px;
  font-weight: bold;
  height: 5.5vh;
  margin-top: 30%;
  text-align: center;
  width: 6vw;
}

.search-wrapper {
  align-items: center;
  display: flex;
  height: 9rem;
  justify-content: space-around;
  width: 20rem;
}

.movies-container {
  align-content: space-between;
  align-items: flex-start;
  border: 2px solid;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 113vh;
  justify-content: space-around;
  margin-left: 5%;
  overflow: scroll;
  width: 93vw;
}
.movie-card {
  border-radius: 15px;
  height: 45vh;
  margin-top: 2%;
  width: 18vw;
}

.movie-image {
  border-radius: 5px;
  height: 40vh;
  width: 15vw;
}
.single-movie-container {
  height: 100vh;
  width: 100vw;
}

.single-movie {
  display: flex;
  flex-direction: row;
}

.poster {
  border-radius: 10px;
  height: 65vh;
  margin-left: 15%;
  width: 27vw;
}

.movie-info {
  color: white;
  margin-left: 13%;
  width: 43vw;
}

.back-button {
  background-color: #C70039;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 18px;
  font-weight: bold;
  height: 5.5vh;
  margin-bottom: 5%;
  margin-left: 4%;
  margin-top: 5%;
  text-align: center;
  width: 10vw;
}

h3 {
  font-size: 23px;
  color: yellow; 
}

p {
  color: yellow;
  font-size: 18px;
}

.info {
  color: white; 
}

.movie-title {
  font-size: 45px;
  margin-left: 0;
  margin-top: 1%;
}
.error-container {
  display: flex;
  flex-direction: column;
  justify-items: center;
}

h2 {
  color: white;
  font-size: 40px;
  margin-left: 39.5%;
}

.error-button {
  background-color: #C70039;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 20px;
  font-weight: bold;
  height: 5.5vh;
  margin-left: 47%;
  margin-top: 2%;
  text-align: center;
  width: 12vw;
}
